.page-header__logo-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  height: 70px;
  background-color: #283645;
  box-shadow: 0 1px 0 0 #000000, inset 0 -1px 0 0 rgba(255, 255, 255, 0.15);
}
.page-header__logo-link {
  height: 40px;
  cursor: pointer;
}
.page-header__logo-mobile {
  position: absolute;
  top: 16px;
  left: 20px;
  fill: #ffffff;
}
.page-header__logo-mobile:hover {
  fill: #d22856;
}
.page-header__logo-mobile:focus {
  fill: #d22856;
}
.page-header__logo-mobile:active {
  fill: rgba(255, 255, 255, 0.3);
}
.page-header__toggle {
  display: none;
  position: absolute;
  top: 20px;
  right: 20px;
  width: 25px;
  height: 26px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: transparent;
  border-style: none;
  outline: none;
  cursor: pointer;
}
.page-header__toggle-icon {
  width: auto;
  height: auto;
  fill: #ffffff;
}
.page-header__toggle-icon:hover {
  fill: #d22856;
}
.page-header__toggle-icon:focus {
  fill: #d22856;
}
.page-header__toggle-icon:active {
  fill: rgba(210, 40, 86, 0.3);
}
.page-header__logo-desktop {
  display: none;
}
.page-header__logo-tablet {
  display: none;
}
@media (min-width: 660px) {
  .page-header__background-wrapper {
    background-color: #283645;
  }
  .page-header__logo {
    height: 40px;
    cursor: pointer;
  }
  .page-header__logo-wrapper {
    min-height: 109px;
  }
  .page-header__logo-tablet {
    display: block;
    position: absolute;
    top: 25px;
    left: 20px;
    fill: #ffffff;
  }
  .page-header__logo-tablet:hover {
    fill: #d22856;
  }
  .page-header__logo-tablet:focus {
    fill: #d22856;
  }
  .page-header__logo-tablet:active {
    fill: rgba(255, 255, 255, 0.3);
  }
  .page-header__logo-mobile {
    display: none;
  }
  .page-header__logo-desktop {
    display: none;
  }
  .page-header__toggle {
    top: 42px;
    right: 20px;
    width: 25px;
    height: 26px;
  }
}
@media (min-width: 960px) {
  .page-header {
    position: absolute;
    top: 1px;
    left: 160px;
  }
  .page-header__background-wrapper {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    width: 960px;
    margin: 0 auto;
    /*временно для сетки*/
  }
